* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
body {
  margin: 0;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgb(196, 205, 252);
  overflow: hidden;
}

.result {
  height: 80px;
  width: 100%;
  border-radius: 5px;
  background: rgb(246,224,255);
  background: radial-gradient(circle, rgba(246,224,255,1) 37%, rgba(245,248,62,1) 100%);
  box-shadow: 2px 2px 5px rgba(0,0,0,0.5);
}

.result p {
  font-size: 37px;
  margin: 5px;
  text-align: right;
  font-family: 'Balsamiq Sans', cursive;
  color: rgba(118, 65, 241, 0.747);
  word-break: break-all;
}

.keypad {
  color:rgba(118, 65, 241, 0.596);
  font-family: 'Balsamiq Sans', cursive;
}

/* .container {
  padding: 0 !important;
  margin: 0 !important;
} */

.calculator-body {
  width: 250px;
  height: 400px;
  margin: auto;
  font-family: 'Balsamiq Sans', cursive;
  color: rgb(156, 92, 216);
}

.button {
  display: block;
  background-color: rgb(243, 166, 230);
  font-family: 'Balsamiq Sans', cursive;
  border-radius: 5px;
}

button {
  width: 25%;
  height: 60px;
  font-size: 30px;
  font-family: 'Courier New', Courier, monospace;
  background-color: rgb(223, 200, 250);
  border-radius: 5px;
  -webkit-text-stroke: 1px rgb(241, 196, 255);
}

button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

button:hover {
  opacity: 0.75;

}
h1 {
  color: rgb(245, 248, 62);
  font-family: 'Balsamiq Sans', cursive;
  text-align: center;
  font-size: 40px;
  text-shadow: 2px 2px 5px rgba(235, 142, 247, 0.589);
  margin-top: 0px;
}
